.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(100, 99, 99);
    margin : 20px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    background:#fff;
    align-items: center;
    min-width:40%
}

.image{
    border-radius: 50%;
    width:100px;
    height:100px
}